@import "./fonts.scss";
// @import "./fontawesome.scss";
// @import "fonts";
@import "./variables.scss";
@import "./animate.scss";
@import "./mixins.scss";
//** TRANVERSAL **
html {
  font: 300 62.5% "Geomanist";
}

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  line-height: 1.5;
  text-rendering: optimizeLegibility;
  font-size: 14px;
}

h3 {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
}

.Hide {
  // display: none;
  opacity: 0.3;
}

.Disabled {
  opacity: 0.3;
  pointer-events: none;
}

input,
select,
textarea,
button {
  font-family: "Lato", sans-serif;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

input,
select,
textarea {
  @include appearance(none);
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  background-color: transparent;
  border: 0 none;
  padding: 1rem;
  transition: all 300ms ease;
  height: 4.5rem;
  background-color: var(--first);
  color: #fff;
  font-size: 1.4rem;
  &:hover {
    border-color: var(--titles);
  }
  &:focus {
    outline: 0 none;
  }
  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus,
  &:-webkit-autofill:active {
    -webkit-text-fill-color: #666 !important;
    transition: background-color 5000s ease-in-out 0s;
  }
}

.ButtonReset {
  @include appearance(none);
  background: #f42534;
  border-radius: 0px;
  border: 0px;
  outline: 0px;
  padding: 0px;
}

.Btn {
  @extend .ButtonReset;
  cursor: pointer;
  // padding: 10px;
  padding: 9px 20px 10px;
  width: auto;
  height: auto;
  min-width: 3.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 300ms ease;
  font-size: 1.4rem;
  font-weight: bold;
  // background: var(--bg-back);
  // background:#ff7204;;
  margin: 0 0.4rem;
  color: #fefffc;
  // @include border-radius(0.8rem);
  transition: all 0.35s ease;
  border-radius: 5px;
  // border: 1px solid #ff7204;
  &:hover {
    // background:  #ff7204;
    color: var(--bg-back);
  }
  &.Rounded {
    @include border-radius(18px);
  }
  &.BtnTransparent {
    background: transparent;
    border: none;
    &:hover {
      background: transparent;
    }
  }
  i {
    margin-left: 0.4rem;
    font-size: 1rem;
  }
  // &.BtnHeader{
  
  //   // &:hover {
  //   //   background:  #ff7204;
  //   //   color: var(--bg-back);
  //   // }
  // }
}

//** FIN TRANVERSAL **
.MainLauncher {
  width: 100%;
  max-width: 200px;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  z-index: 99999999;
  animation-name: cognitive-assistant-fade-in-up;
  animation-duration: 1s;
  animation-fill-mode: both; // box-shadow: 0 0 1rem 0 rgba(175, 27, 27, 0.83);
  transition: all 200ms linear;
  .LauncherButton {
    border: 0 none;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    background-color: var(--laucher);
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
    margin-left: 68%;
    // transition: all 300ms ease;
    // animation-name: cognitive-assistant-fade-in-up;
    // animation-duration: 1s;
    // animation-fill-mode: both;
    &:hover {
      transform: scale(1.1);
    }
    &:focus {
      outline: 0 none;
    }
    i {
      transition: all 200ms linear;
      display: block;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      &.IconLauncher {
        // background-image: url("../images/icon-assistant-jose.svg");
        // height: 3.3rem;
      }
      &.IconClose {
        background-image: url("../images/icon-close.svg");
        height: 0;
      }
    }
    &.Close {
      margin-left: 68%;
      .IconLauncher {
        height: 0rem;
      }
      .IconClose {
        height: 3.3rem;
      }
    }
  }
  .BubbleLauncher {
    width: 1.8rem;
    height: 1.8rem;
    position: absolute;
    // top: 0rem;
    // left: 0rem;
    top: 4.5rem;
    left: -0.5rem;
    font-size: 1.3rem;
    font-weight: 600;
    text-align: center;
    color: #fff;
    background-color: var(--error);
    border-radius: 100%;
    &:after {
      content: "";
      border-radius: 50%;
      position: absolute;
      left: 45%;
      top: 45%;
      border: 1px solid var(--error);
      opacity: 0;
      -webkit-animation: pulso 0.8s infinite linear;
      animation: pulso 0.8s infinite linear;
    }
  }
  .Notification {
    position: relative;
    bottom: 2rem;
    right: 85px;
    max-width: 28rem;
    width: 28rem;
    padding: 1.6rem;
    padding-right: 4.2rem;
    box-shadow: 0 0 3rem 0 rgba(0, 0, 0, 0.1);
    background-color: #fff;
    animation: notification 2s infinite linear;
    border-radius: 2.4rem 2.4rem 0 2.4rem;
    line-height: 1.8rem;
    span {
      color: grey;
      font-size: 14px;
      font-weight: bold;
    }
    .Title {
      font-weight: bold;
      font-size: 14px;
      color: #333;
    }
    .ButtonClose {
      display: block;
      border: 0 none;
      position: absolute;
      top: 10px;
      right: 10px;
      background: transparent;
      width: 24px;
      height: 24px;
      overflow: hidden;
      cursor: pointer;
      font-size: 14px;
      color: #333;
      .IconClose {
        background-image: url("../images/icon-close.svg");
        width: 32px;
        height: 32px;
        display: inline-block;
        background-position: -44px 0px;
        margin: -3px 0 0 -2px;
      }
    }
  }
}

.MainAssistant {
  animation: inAssistant 0.4s linear;
  overflow: hidden;
  border-radius: 0.7rem;
  box-shadow: 0 0 1.5rem 0 rgba(0, 0, 0, 0.2);
  display: grid;
  // margin: 1rem 1rem 0rem 1rem;
  margin: 6rem 1rem 0rem 1rem;
  grid-template: {
    rows: 6rem calc(100vh - 20.7rem) 5.7rem;
    columns: 6rem auto;
    areas: "header header" "conversation conversation" "input input";
  }
  &.HeaderMore {
    // grid-template: {
    //     rows: 48% calc(52% - 5.7rem) 5.7rem;
    // }
    height: calc(100vh - 9rem);
  }
  .Header,
  .HeaderHelpDown {
    z-index: 2;
    grid-area: header;
    box-shadow: 3px 2px 10px rgba(0, 0, 0, 0.3);
    transform: translate3d(0, 0vh, 0);
    transition: all 200ms ease;
    grid-column: 1 / 3; // background-image: url("../images/nodos.svg"), linear-gradient(45deg, #004ecb 2%, #0957d6 61%, #2979ff 97%);
    background-color: var(--first);
    display: grid;
    grid-template: {
      columns: 6rem auto 6rem;
      rows: 5rem 3rem;
    }
    align-items: center;
    overflow: hidden;
    // overflow-y: scroll;
    height: 7.7rem;
    &.Large {
      &:before {
        content: "";
        background: linear-gradient(
          to top,
          rgb(255, 255, 255),
          rgba(0, 0, 0, 0)
        );
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 10;
        width: 100%;
        height: 50%;
      }
    }
    &.HeaderMore {
      grid-template-columns: 2rem calc(100% - 8.6rem) 5rem 1.6rem;
      grid-template-rows: 6rem auto;
      justify-content: space-between;
      height: 100%;
      box-shadow: none;
      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: -2;
      }
      .HeaderImage {
        justify-self: left;
      }
      .HeaderText {
        grid-column: 2 / 4;
        // margin-right: 2rem;
        align-self: start;
        padding: 0.5rem;

        h3 {
          font-weight: 300;
          font-size: 2.1rem;
          margin-bottom: 1rem;
        }
        p {
          opacity: 1;
          font-size: 2rem;
          font-weight: 300;
        }
      }
    }
    .HeaderImage {
      width: 100%;
      margin-top: 2rem;
      max-width: 5rem;
      justify-self: center;
      object-fit: cover;
      border-radius: 100%;
      margin-left: 2rem;
    }
    .HeaderText {
      color: #fff;
      margin-top: 2rem;
      margin-left: 2rem;
      cursor: pointer;
      padding: 0.5rem;
      border-radius: 1rem;
      transition: all 0.3s ease-in-out;
      &:hover {
        background: rgba(0, 0, 0, 0.1);
      }
      h3 {
        line-height: 1.6rem;
      }
      p {
        opacity: 0.75;
        font-size: 1.1rem;
        font-weight: normal;
      }
    }
    .BtnTransparent {
      justify-self: center; // align-self: start;
      // min-height: 3.6rem; // margin-top: 0.7rem;
      transition: all 300ms ease-in-out;
      opacity: 1;
      // cursor: pointer;
      .IconMinimized {
        position: absolute;
        left: 28.5rem;
        top: 2.2rem;
        cursor: pointer;
        background: white;
        width: 1.3rem;
        height: 0.1rem;
        transition: all 0.3s ease-in-out;
        border-radius: 2rem;
        opacity: 1;
        //  grid-column: 3/ 4;
        // margin-left: 6rem;
        margin-bottom: 0;
        // margin-bottom: 1rem;
      }
      // &:hover {
      //     opacity: 0.8;
      //     background: rgba(0, 0, 0, 0.1);
      //     border-radius: 1rem;
      // }
      &:focus {
        outline: 0 none;
      }
      &.Disabled {
        opacity: 0.4;
        pointer-events: initial;
      }
      .CloseButton {
        background-image: url("../images/icon-close.svg");
        position: absolute;
        top: 1.2rem;
        left: 30.5rem;
        width: 2.5rem;
        height: 1.2rem;
        // grid-column: 4/ 6;
        // // grid-row: 1/ 1;
        background-position: 50% 50%;
        background-size: 2.5rem; 
        // margin-bottom: 1rem;// justify-self: start;
      }
      &.ButtonHelp {
        background-image: url("../images/icon-help.svg");
        background-repeat: no-repeat;
        width: 6rem;
        height: 2.6rem;
        background-position: 0.6rem 0.5rem;
        background-size: 5.3rem;
        border-color: #f2f2f2;
        // border-radius: 80px;
        border: 1px solid;
        
        grid-column: -3/-1;
        grid-row: -3/ -1;
        margin-top: 3.2rem;
        margin-right: 4rem;

        &:hover {
          color:#666;
            // border-radius: 1rem;
        }
        &:focus {
          outline: 0 none;
        }
        &.Active {
          box-shadow: 0 0 1rem #fff;
        }
        &.Disabled {
          pointer-events: none;
        }
      }
    }
  }
  .HeaderHelpDown {
    grid-template: {
      columns: 6rem auto 5rem;
    }
  }
  .HeaderHelpUp {
    grid-template: {
      columns: 6rem auto 3.6rem 3.6rem 1.5rem;
    }
  }
  .AssistantHelper {
    padding: 2rem 3rem;
    z-index: 1;
    grid-area: helper;
    font-size: 1.3rem;
    transition: left 300ms ease-in-out;
    grid-column: 1 / 3;
    grid-row: 2 / 2;
    background: linear-gradient(to right, var(--gradient-medium), var(--first));
    overflow-y: auto;
    overflow-x: hidden; // height: 0%;
    position: relative;
    left: 100%;
    height: 70%;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
    &.Active {
      left: 0%;
      & + .MainAssistant {
        grid-template: {
          areas: "header header" "helper helper" "input input";
        }
      }
    }
    h3 {
      // color: var(--bg-back);
      color: #eee;
      font-size: 2.2rem;
      font-weight: 500;
    }
    p {
      font-size: 1.4rem;
      font-weight: 300;
    }
    &::-webkit-scrollbar {
      width: 6px;
    }
    &::-webkit-scrollbar-thumb {
      background: var(--gray-light);
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &.inactive:before {
      content: "";
      background-color: var(--bubble-bg);
      width: 100%;
      height: 100%;
      position: fixed;
      z-index: 10;
    }
    .Bloqueo {
      opacity: 0;
      transition: all 0.3s linear;
      height: 0;
      background-color: var(--gray);
      font-weight: 100;
      position: fixed;
      top: 7rem;
      left: 0px;
      width: 100%;
      z-index: 100;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1rem;
      color: var(--bg-back);
      &.HelpBloqued {
        opacity: 1;
        height: auto;
      }
    }
    .Subtittle {
      font-weight: 400;
      margin: 1rem 0rem 2rem 0rem;
      color: #fff;
    }
    .IconLi {
      // background: url("../images/angle-down.svg") no-repeat 50% 50%;
      height: 1rem;
      width: 2rem;
      margin-right: 1.1rem;
      path {
        fill: var(--first);
      }
    }
    .AssistantHelperChild {
      cursor: pointer;
      background: #f2f2f2;
      border-radius: 0.5rem;
      flex-flow: column;
      margin: 0 0rem 0.5rem;
      &:last-child {
        margin-bottom: 25px;
      }
      .AssistantTitle {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 10px 0px;
        transition: all 0.35s ease-in-out;
        &:hover {
          background-color: #fff;
          a,
          i {
            color: var(--gradient-medium);
          }
        }
        &:last-child,
        &:first-child {
          border-radius: 5px;
        }
        // &.active{
        //   a{
        //     color: #000;
        //   }
        // }
      }
      a {
        font-weight: 680;
        padding-left: 1.5rem;
        color: var(--first);
        font-size: 1.3rem;
        text-decoration: none;
        &:hover {
          color: var(--gradient-medium);
        }
      }
      .LiChild {
        border-bottom: 1px solid #dadada;
        padding: 1rem 0px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        a {
          padding-left: 20px;
        }
        &:first-child {
          border-top: 1px solid #dadada;
        }
        &:last-child {
          border-radius: 0px 0px 5px 5px;
          border-bottom: none;
        }
        &:hover {
          background-color: #fff;
          color: var(--first);
          a {
            color: var(--gradient-medium);
          }
        }
      }
      .NodoAbuelo {
        max-height: 0;
        transition: max-height 0.3 ease-out;
        overflow: hidden;
        &.Active {
          max-height: 500px;
          transition: max-height 0.3s ease-in;
        }
        .LiChild a {
          padding-left: 20px;
        }
        p {
          font-size: 13px;
          padding: 5px 15px 10px 15px;
          margin-bottom: 0;
        }
      }
    }
  }

  .fade-show {
    height: calc(100% + 0.1rem);
    grid-area: conversation;
    grid-row: 2 / 3;
    overflow: hidden;
    overflow-y: scroll;
    background: #fff;
    transition: all 300ms ease-in-out;
    grid-column: 1 / 3;
    background-image: linear-gradient(
      -180deg,
      rgba(255, 255, 255, 0) 50%,
      black 100%
    );
    padding: 1.6rem 0.8rem 1.6rem 1.6rem;
  }

  .ConversationHolder {
    height: calc(100% + 0.1rem);
    grid-area: conversation;
    grid-row: 2 / 3;
    overflow: hidden;
    overflow-y: scroll;
    background: white;
    transition: all 300ms ease-in-out;
    grid-column: 1 / 3;
    // background-image: linear-gradient(-180deg, rgba(255, 255, 255, 0) 50%, #f7f7f7 100%);
    padding: 1.6rem 0.8rem 1.6rem 1.6rem;


    &.holder {
      height: calc(100vh - 5rem);
      opacity: 0.3;
    }
    &::-webkit-scrollbar {
      width: 6px;
    }
    &::-webkit-scrollbar-thumb {
      background: var(--gray-light);
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &.Active {
      height: calc(50vh - 4rem);
      margin-top: calc(50vh - 4rem);
    }
    &.Show {
        z-index: 1;
        background:rgba(0,0,0,.35);
        opacity: 0.9;
        z-index: 1;
        .Bubble{
          z-index: 0;
            display: none;
         
        }
        .ConversationBubble{
          z-index: 0;
            img{
                display: none;
            }
        }

        .ConversationBubbleForm{
            img{
                display: none;
            }
        }
    }
    .ConversationBubble {
      margin-bottom: 0.8rem;
      display: flex;
      width: 100%;
      align-items: flex-end;
      justify-content: flex-start;
      position: relative;
      flex-wrap: wrap;
      z-index: 0; // display: grid;
      // grid-template-columns: 4.5rem auto;
      // margin-bottom: .8rem;
      .Bubble {
        // background-color: #d8d7db;
        background-color: #ebebeb;
        padding: 1.2rem 1.6rem;
        font-weight: 500;
        // font-size: 1.4rem;
        color: #6e6b6a;
        // color: black;
        word-break: break-word;
        // border-radius: 0.9rem;
        border-radius:  15px 15px 0 15px;
        margin-left: 0;
        max-width: calc(100% - 3.8rem);
        margin-top: 0.5rem;
        ul,
        ol {
          margin: 1.6rem;
        }
        img {
          width: 88%;
          border-radius: 0%;
        }
      }
      // &.From {
      //     .ConversationBubble{
      //         .Bubble{
      //             border-radius: 1.5rem;
      //         }
      //     }
      //     .Bubble {
      //         &:last-child{
      //             border-radius: 1.5rem 1.5rem 1.5rem 0;
      //         }
      //     }
      // }
      &.To {
        justify-content: flex-end;
        .Bubble {
          // &:first-child{
          //     border-radius: 1.5rem 1.5rem 0 1.5rem;
          // }
          background-color: var(--first);
          color: var(--bubble-bg-message-text-send);
        }
      }
      img {
        width: 3.8rem;
        margin: 0 !important;
        border: none;
        align-self: flex-end;
        // border-radius: 90%;
        object-fit: cover;
      }
      &.Buttons {
        justify-content: center;
        margin-bottom: 0rem;
        .Btn {
          margin-bottom: 0.8rem;
        }
      }
      &.Datepickers {
        justify-content: space-between;
      }
      .Assessment {
        display: block;
        padding: 0.5rem 1.5rem;
        background: rgba(0, 0, 0, 0.1);
        margin: 0.8rem -1.5rem -1.5rem -1.5rem;
        border-radius: 0 0 3rem 3rem;
        a {
          padding: 0.8rem;
          display: inline-block;
          text-decoration: none;
          font-weight: normal;
        }
      }
      // &.from{
      //   img{
      //     border:2px solid $primary;
      //   }
      // }
      .BubbleFile {
        width: 100%;
        border-radius: 0.8rem;
        overflow: hidden;
        display: flex;
        justify-content: center;
        padding: 0.8rem;
        margin-bottom: 0.8rem;
        align-items: center;
        background-color: var(--first);
        font-size: 1.4rem;
        &.Img {
          padding: 0;
          img {
            width: 100%;
          }
        }
        i {
          margin-left: 0.4rem;
          color: var(--bg-back);
        }
        a {
          color: #fff;
          &:hover {
            color: var(--gray-light);
          }
        }
      }
      &.Attach {
        .BtnAttach {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          max-width: 200px;
          padding: 0.8rem;
          font-size: 1.4rem;
          background: var(--bg-back);
          color: var(--first);
          i {
            margin-left: 1rem;
          }
          input {
            display: none;
          }
          &:hover {
            background: var(--first);
            color: var(--bg-back);
          }
        }
        p {
          &.Error {
            color: var(--error);
            font-size: 1.4rem;
            margin-top: 0.5rem;
          }
        }
        justify-content: center;
      }
      .Select {
        width: 100%;
        position: relative;
        &:before {
          content: "";
          position: absolute;
          top: 1.4rem;
          right: 1.5rem;
          width: 0;
          height: 0;
          border-left: 0.8rem solid transparent;
          border-right: 0.8rem solid transparent;
          border-top: 1rem solid var(--gray-dark);
          z-index: 11;
        }
        .Options {
          width: 100%;
          margin: 0;
          height: 3.5rem;
          background: transparent;
          color: var(--gray-dark);
          font-weight: 300;
          overflow: hidden;
          display: flex;
          flex-flow: column;
          font-size: 1.4rem;
          &.Active {
            overflow: initial;
            div {
              &:first-child {
                border-radius: 0.4rem 0.4rem 0px 0px;
              }
            }
            &.Error {
              div {
                &:first-child {
                  border-radius: 0.4rem 0.4rem 0px 0px;
                }
              }
            }
          }
          &.Error {
            div {
              &:first-child {
                border-radius: 0.4rem;
                border: 0.1rem solid;
                border-color: var(--error);
                color: var(--error);
              }
            }
          }
          div {
            cursor: pointer;
            padding: 0.6rem 1.8rem;
            z-index: 10;
            background: var(--bg-back);
            position: relative;
            border-right: 0.1rem solid var(--border-color);
            border-bottom: 0.1rem solid var(--border-color);
            border-left: 0.1rem solid var(--border-color);
            &:hover {
              background: var(--white-gray);
            }
            &:first-child {
              border-radius: 0.4rem;
              border: 0.1rem solid var(--border-color);
            }
            &:last-child {
