      }
      &.Transcript {
        display: flex;
        align-items: center;
        justify-content: space-around;
        .IconStop {
          background-image: url("../images/icon-stop.svg");
          background-color: var(--error);
          border-radius: 50%;
        }
        .IconPlane {
          background-image: url("../images/icon-send-rounded.svg");
          background-color: var(--success);
          border-radius: 50%;
        }
        .Timer {
          min-width: 3.5rem;
          animation: inAssistant 0.4s linear;
          p {
            font-weight: 600;
            font-size: 1.4rem;
          }
        }
      }
    }
  }
  .Loader {
    grid-column: 1 / 3;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f7f7f7;
    .Dot {
      display: inline-block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      margin-right: 3px;
      background-color: var(--first);
      animation: in 1s linear infinite;
      &:nth-child(2) {
        animation-delay: -0.8s; // background-color: var(--first);
        // opacity: .8;
      }
      &:nth-child(3) {
        animation-delay: -0.6s; // background-color: var(--first);
        // opacity: .4;
      }
    }
  }
  .iframe-cognitive-assistant-container-minimized {
    transform: translate3d(0, calc(100vh - 6rem), 0);
    padding: 0.7rem;
    padding-left: 1.4rem;
    height: 6rem;
  }
  &.Mobile {
    border-radius: 0;
    box-shadow: initial;
    margin: 0;
    grid-template-rows: 6rem calc(100vh - 11.7rem) 5.7rem; // .ConversationHolder {
    //     height: calc(100vh - 11.7rem);
    // }
    // .Header {
    //     grid-template-columns: 6rem auto 3.6rem 3.6rem 4rem;
    // }
    .HeaderHelpDown {
      grid-template-columns: 6rem auto 3.6rem 3.6rem 0.5rem;
    }
    .HeaderHelpUp {
      grid-template: {
        columns: 6rem auto 3.6rem 3.6rem 3.6rem 0.5rem;
      }
    }
    &.HeaderMore {
      grid-template: {
        rows: 43% calc(57% - 5.7rem) 5.7rem;
      }
      height: 100vh;
      .HeaderHelpDown {
        grid-template-columns: 2rem auto 3.6rem 3.6rem 2rem;
      }
      .HeaderHelpUp {
        grid-template: {
          columns: 2rem auto 3.6rem 3.6rem 2rem;
        }
      }
      .HeaderText {
        grid-column: 2 / 5;
        margin: 0;
      }
    }
    .Bloqueo {
      top: 6rem;
    }
    .LogoCognitiva {
      display: none;
    }
  }
  // .LogoCognitiva {
